.loading-rocket {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    &.big {
        top: 0;
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 999;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 0;
        background-color: #fff;

    }
    img {
        width: 250px;
    }
    .load {
        background: rgba(255, 255, 255, 0.1);
        justify-content: flex-start;
        align-items: center;
        position: relative;
        padding: 0 5px;
        display: flex;
        height: 5px;
        width: 300px;
        transition: 0.8s ease-in-out;
        margin-top: 10px;
        margin-bottom: 5px;
        border-radius: 10px;
        .loadingBar {
            animation: load 3s normal forwards;
            box-shadow: 0 10px 40px -10px #fff;
            background: rgb(32, 121, 50);
            height: 5px;
            width: 0;
            position: absolute;
            left: 0;
            border-radius: 10px;
        }
        &.hideLoading {
            opacity: 0;
        }
    }
}
